<template>
    <div class="setting-basis">
        <h1 class="component-name">{{ title }} {{ $attrs.currentIndex }}</h1>
        <div class="basis-component">
            <el-row
                type="flex"
                align="middle"
                class="mt30"
                v-if="type === 'ElementScroll' || type === 'ElementNewBanner' "
            >
                <p class="text">单张图片宽度：</p>
                <el-input
                    class="setWidth"
                    type="number"
                    placeholder="number"
                    size="small"
                    min="0"
                    max="750"
                    v-model.number="widthVal"
                >
                    <template slot="append">px</template>
                </el-input>
            </el-row>

            <el-row type="flex" align="middle" class="mt30">
                <p class="text">整行高度：</p>
                <el-input
                    class="setWidth"
                    type="number"
                    placeholder="number"
                    size="small"
                    min="0"
                    v-model.number="heightVal"
                >
                    <template slot="append">px</template>
                </el-input>
            </el-row>

            <el-row type="flex" align="middle" class="mt30" v-if="type === 'ElementNewBanner' ">
                <p class="text">背景图片：</p>
                <!-- TODO: 背景图片上传 -->
                <el-upload
                    class="avatar-uploader"
                    action="#"
                    :show-file-list="false"
                    :on-change="setBackgroundImg "
                    :before-upload="beforeAvatarUpload"
                >
                    <!-- :on-success="handleAvatarSuccess" -->
                    <img v-if=" background_img" :src="background_img" class="avatar" />
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
            </el-row>

            <el-row class="mt30">
                <el-button
                    type="primary"
                    icon="el-icon-circle-plus-outline"
                    @click="setAttribute"
                >添加图片</el-button>
            </el-row>

            <div>
                <el-row
                    type="flex"
                    align="middle"
                    class="mt30"
                    v-for="(item, index) in images"
                    :key="index"
                >
                    <el-col :span="3">
                        <el-button
                            type="danger"
                            icon="el-icon-delete"
                            circle
                            @click="images.splice(index, 1)"
                        ></el-button>
                    </el-col>
                    <el-col :span="8">
                        <!-- TODO: 上传图片 -->
                        <el-upload
                            class="avatar-uploader"
                            action="#"
                            :show-file-list="false"
                            :on-change="file => item.url = handleChange(file)"
                            :before-upload="beforeAvatarUpload"
                        >
                            <!-- :on-success="handleAvatarSuccess" -->
                            <img v-if="item.url " :src="item.url" class="avatar" />
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                    </el-col>

                    <el-col :span="13">
                        <el-select
                            v-model="item.path"
                            placeholder="请选择需要跳转的页面"
                            clearable
                            class="mb30"
                        >
                            <el-option
                                v-for="selectItem in options"
                                :key="selectItem.value"
                                :label="selectItem.label"
                                :value="selectItem.value"
                            ></el-option>
                        </el-select>
                        <el-select v-model="item.shop_id" placeholder="请选择商品种类" clearable>
                            <el-option
                                v-for="selectItem in options"
                                :key="selectItem.value"
                                :label="selectItem.label"
                                :value="selectItem.value"
                            ></el-option>
                        </el-select>
                    </el-col>
                </el-row>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            options: [
                {
                    value: "黄金糕",
                    label: "黄金糕"
                },
                {
                    value: "双皮奶",
                    label: "双皮奶"
                },
                {
                    value: "蚵仔煎",
                    label: "蚵仔煎"
                },
                {
                    value: "龙须面",
                    label: "龙须面"
                },
                {
                    value: "北京烤鸭",
                    label: "北京烤鸭"
                }
            ]
        };
    },
    props: {
        type: String,
        width: Number,
        height: Number,
        images: Array,
        background_img: String
    },
    computed: {
        widthVal: {
            get() {
                return this.width * 2;
            },
            set(value) {
                if (value > 750)
                    return this.$message.warning("图片宽度最大为750px");
                this.$emit("update:width", value / 2);
            }
        },
        heightVal: {
            get() {
                return this.height * 2;
            },
            set(value) {
                this.$emit("update:height", value / 2);
            }
        },
        title() {
            switch (this.type) {
                case "ElementBanner":
                    return "轮播图样式一";
                case "ElementNewBanner":
                    return "轮播图样式二";
                case "ElementBasis":
                    return "普通组件";
                case "ElementScroll":
                    return "滑动组件";
            }
        }
    },
    methods: {
        /**
         *  图片数量设置
         */
        setAttribute() {
            if (this.type === "ElementScroll") {
                this.images.push({ url: "", shop_id: "", path: "" });
            } else if (this.type === "ElementBanner") {
                this.images.length < 10
                    ? this.images.push({
                          url: "",
                          shop_id: "",
                          path: ""
                      })
                    : this.$message.warning("最多添加十张图片");
            } else {
                this.images.length < 5
                    ? this.images.push({
                          url: "",
                          shop_id: "",
                          path: ""
                      })
                    : this.$message.warning("最多添加五张图片");
            }
        },
        /**
         *  TODO: 轮播图样式二背景图片设置
         */
        setBackgroundImg(file) {
            if (this.type === "ElementNewBanner") {
                const url = URL.createObjectURL(file.raw);
                this.$emit("update:background_img", url);
            }
        },
        /**
         *  TODO: 上传校验
         */
        beforeAvatarUpload(file) {
            const arr = [
                "image/jpeg",
                "image/jpg",
                "image/png",
                "image/gif",
                "image/webp"
            ];
            const isImg = arr.includes(file.type);

            if (!isImg) {
                this.$message.error("上传文件的格式不对！");
            }
            return isImg;
            // const isLt2M = file.size / 1024 / 1024 < 2;
        },

        /* TODO: 图片临时地址 */
        handleChange(file) {
            return URL.createObjectURL(file.raw);
        }
    }
};
</script>

<style scoped>
.component-name {
    border-bottom: 1px dashed #dfe6e9;
    padding-bottom: 10px;
}

.setWidth {
    width: 150px;
}

/* TAG: 右侧配置栏固定高度 */
.basis-component {
    height: calc(100vh - 170px);
    overflow: auto;
}

.text {
    width: 120px;
    text-align-last: justify;
}
</style>



